<script>
import HeaderEl from './common/Header.vue';

export default {
  name: 'HomeW',
  components: {
    HeaderEl
  },
  data: () => ({
    sceneName: '다이어트 일기',
    isHome: true,
    year: 0,
    month: 0,
    day: 0,
  }),
  created() {
    this.init();
  },
  methods: {
    init(param) {
      if (param) {
        this.year = param[0];
        this.month = param[1];
        this.day = param[1];
      } 
      else {
        const date = new Date();
        this.year = date.getFullYear();
        this.month = date.getMonth() + 1;
        this.day = date.getDate();
      }
    }
  }
}
</script>

