<template>
  <HeaderEl
    class="header"
    :name="sceneName">
  </HeaderEl>

  <!-- 본문 -->
  <main class="wrap" @onScroll="onScroll">
    
    
  </main>
</template>

<script>
import HeaderEl from './common/Header.vue';

export default {
  name: 'UiCompo',
  components: {
    HeaderEl,
  },
  data: () => ({
    sceneName: 'UI 컴포넌트',
  }),
  mounted() {
    this.onScroll();
    window.addEventListener("scroll", this.onScroll, true)
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.onScroll, true)
  },
  methods: {
    onScroll() {
      // console.log('scrolled');
      // this.windowTop = e.target.scrollTop;

      const selector = document.querySelector('.wrap'),
            header = document.querySelector('.header');
      
      this.windowTop = window.top.scrollY;
      this.scrolledTop = selector.scrollTop;
      console.log(this.windowTop, this.scrolledTop);

      if(this.scrolledTop > 0) {
        header.classList.add('scrolled');
      } else {
        header.classList.remove('scrolled');
      }
    }
  }
}
</script>

